import React, { Component } from 'react';
import Examnonlabviewtable from './ExamNonlabtablecell'
import { Input, Select, Modal } from 'antd';
import * as ApiGithub from '../../API/ApiGithub'
import * as ApiProvider from '../../API/ApiProvider'
import InsReportModal from '../ExamReportModal'
import moment from 'moment'

// var mode_class, mode_state;
class Nonlabview extends Component {
    state = {
        ClassMode: "全部類別",
        StateMode: 1,
        InputMode: null,
        parser: { "mappings": [] },
        modalVisible: false,
        modalReport: "",

        preHID: null,
        prestartTime: null,
        preendTime: null,
        ORDLIST: [],
        RESSECT: []
    }

    componentDidMount() {
        const { caseinfo, PickerDateRange } = this.props
        const HID = caseinfo.HISID
        const startTime = moment(PickerDateRange[0]).format("YYYY-MM-DD")
        const endTime = moment(PickerDateRange[1]).format("YYYY-MM-DD")
        const parseTime = moment(caseinfo.PBASINGNINFO[0].birthday, "YYYYMMDD").format("YYYY-MM-DD")
        this.setState({ preHID: HID, prestartTime: startTime, preendTime: endTime })
        this.getAPI(HID, parseTime)
    }
    componentDidUpdate() {
        const { caseinfo, PickerDateRange } = this.props
        const { preHID, prestartTime, preendTime } = this.state
        const HID = caseinfo.HISID
        const startTime = PickerDateRange[0]
        const parseTime = moment(caseinfo.PBASINGNINFO[0].birthday, "YYYYMMDD").format("YYYY-MM-DD")
        const endTime = PickerDateRange[1]
        if (HID !== preHID || startTime !== prestartTime || endTime !== preendTime) {
            this.setState({ preHID: HID, prestartTime: startTime, preendTime: endTime })
            this.getAPI(HID, parseTime)
        }
    }
    getAPI(HID, parseTime) {
        function selectMonthInterval(storeProcedureTime) {
            const timestampInterval = moment() - moment(storeProcedureTime, "YYYY-MM-DD").valueOf()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const loginAcc = sessionStorage.getItem('user');
        ApiProvider.getStoredProcedureRESSECT(HID, selectMonthInterval(parseTime), "ALL", loginAcc)
            .then(data => { this.setState({ RESSECT: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESSECT NG " + JSON.stringify(err)) });

        ApiProvider.getStoredProcedureORDLIST(HID, parseTime, "ALL", "DOC3924B")
            .then(data => {
                const Row = data.data
                if (Array.isArray(Row)) {
                    const filterRow = Row.filter(function (info, index, arr) {
                        return !(Number(info.ORSTATUS) === 60 || Number(info.ORSTATUS) === 61 || Number(info.ORSTATUS) === 82)
                    })
                    this.setState({ ORDLIST: filterRow })
                }
            })
            .catch(err => { console.log(err) })
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "NonLabExamReport");
                parser.mappings = [{ "key": "全部類別", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson ExamReportNonLab OK " + JSON.stringify(parser))
                this.setState({
                    parser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    Inputvaluesearch = (event) => {
        this.setState({ InputMode: event.target.value })
    }

    onChangeState = (value) => {
        this.setState({ StateMode: value })
    }

    onChangeClass = (value) => {
        console.log(value)
        this.setState({ ClassMode: value })
    }

    showModal = (report) => {
        this.setState({
            modalVisible: true,
            modalReport: report
        });
    };

    handleOk = e => {
        this.setState({
            modalVisible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            modalVisible: false,
        });
    };

    render() {
        const { Option, caseinfo } = Select;
        const { PickerDateRange } = this.props
        const { modalVisible, modalReport, ORDLIST, RESSECT } = this.state
        const showModal = this.showModal
        const handleOk = this.handleOk
        const handleCancel = this.handleCancel
        return (
            <div>
                <div style={{ backgroundColor: "#71b0e46e", borderRadius: "4px 4px 0 0", height: "36px", padding: "0px 16px", display: "grid", alignItems: "center", gridTemplateColumns: "2fr 3fr" }}>
                    <div style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>檢驗清單及報告</div>
                    <div style={{ display: 'grid', gridTemplateColumns: "130px 100px auto", gridColumnGap: "5px" }}>
                        <Select defaultValue={this.state.ClassMode} placeholder="選擇檢驗類別" style={{ width: 130 }} onChange={this.onChangeClass}>
                            {this.state.parser.mappings.map((mapping, index) => {
                                return <Option key={index} value={mapping.sample ? mapping.key + "-" + mapping.sample : mapping.key}>{mapping.sample ? mapping.key + "-" + mapping.sample : mapping.key}</Option>
                            })}
                        </Select>
                        <Select defaultValue={this.state.StateMode} placeholder="選擇項目狀態" style={{ width: 100 }} onChange={this.onChangeState}>
                            <Option value={0}>全部狀態</Option>
                            <Option value={1}>正式報告</Option>
                            <Option value={2}>已簽收</Option>
                            <Option value={3}>待送檢</Option>
                            <Option value={4}>其他</Option>
                        </Select>
                        <Input id="keywordsearch" placeholder="輸入關鍵字" onChange={this.Inputvaluesearch} />
                    </div>
                </div>
                <Examnonlabviewtable PickerDateRange={PickerDateRange} RESSECT={RESSECT} ORDLIST={ORDLIST} selectedClass={this.state.ClassMode} parser={this.state.parser} StateMode={this.state.StateMode} InputMode={this.state.InputMode} visible={modalVisible} showModal={showModal} handleOk={handleOk} handleCancel={handleCancel}></Examnonlabviewtable>
                {/* <Nonlabviewtable caseinfo={caseinfo} StartTime={PickerDateRange[0]} EndTime={PickerDateRange[1]}></Nonlabviewtable> */}
                <Modal
                    title={"檢驗結果"}
                    visible={modalVisible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    width={1000}
                    footer={null}>
                    <InsReportModal report={modalReport}></InsReportModal>
                </Modal>
            </div>
        );
    }
}

export default Nonlabview
