import React, { Component } from 'react';
import detaillogo from '../Image/svg/details.svg'
import { Tooltip, Icon } from 'antd';
import * as ApiProvider from '../API/ApiProvider'
import moment from 'moment';

class Notecell extends Component {

    onClickDelete() {
        const { info } = this.props
        this.props.callback(info)
    }
    updatetime_format(updatetime) {
        function monthformat(month) {
            return month + 1
        }
        return new Date(updatetime).getFullYear() + "-" + monthformat(new Date(updatetime).getMonth()) + "-" + new Date(updatetime).getDate() + " " + new Date(updatetime).getHours() + ":" + new Date(updatetime).getMinutes()
    }
    controlMode() {
        const { info, EditMode } = this.props
        const updateTime = moment(info.noticekey.updatetime, "YYYYMMDDHHmmss").format("MM/DD HH:mm")
        const text = info.doc + "醫師於" + updateTime + "更新"
        if (EditMode) {
            return (
                <div style={{ display: "grid", gridTemplateColumns: "30px 30px", gridGap: "5px" }}>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }} onClick={() => this.onClickDelete()}>
                        <Icon type="delete" />
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <Tooltip placement="right" title={text}>
                            <div>
                                <img src={detaillogo} alt='detaillogo'></img>
                            </div>
                        </Tooltip>
                    </div>
                </div>
            );
        }
        return (
            <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                <Tooltip placement="right" title={text}>
                    <div>
                        <img src={detaillogo} alt='detaillogo'></img>
                    </div>
                </Tooltip>
            </div>
        );
    }
    render() {
        const { info } = this.props
        return (
            <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", minHeight: "35px" }}>
                <div style={{ wordBreak: "break-all", fontSize: "1.3rem", display: "flex", alignItems: "center" }}>{info.context}</div>
                {this.controlMode()}
            </div>
        );
    }
}

export default Notecell
