import React, { Component } from 'react';
import { Tooltip, Button, Icon, Input } from 'antd';
import moment from 'moment'

export default class NoticeCell extends Component {
    state = {
        EditMode: false
    }

    contextDisplay() {
        const { EditMode } = this.state
        const { value } = this.props
        if (!EditMode) {
            return (
                <div style={{ textAlign: "left", whiteSpace: "pre-wrap", display: "flex", alignItems: "center", fontSize: "16px" }}>{value.context}</div>
            );
        }
        else {
            return (
                <Input id={"editcontext"} defaultValue={value.context} />
            );
        }
    }
    contextDelete() {
        //props function
        const { value } = this.props
        // const text = document.getElementById("editcontext").value
        const newValue = {
            context: "KKK",
            editor: value.editor,
            hisid: value.hisid,
            time: value.patientimportantmatterkey.time,
            transinid: value.patientimportantmatterkey.transinid
        }
        this.props.callback(newValue, "delete");
        // this.setState({ EditMode: false })

    }
    onClickSave() {
        //porps function
        const { value } = this.props
        const text = document.getElementById("editcontext").value
        const newValue = {
            context: text,
            editor: value.editor,
            hisid: value.hisid,
            time: value.patientimportantmatterkey.time,
            transinid: value.patientimportantmatterkey.transinid
        }
        this.props.callback(newValue, "save");
        this.setState({ EditMode: false })
    }
    controlDisplay() {
        function tooltiptext(editor, time) {
            function nullvalue(value) {
                if (value === null || value === undefined) {
                    return "--"
                }
                return value
            }
            const displayTime = moment(time, "YYYYMMDDHHmmss").format("MM/DD HH:mm")
            return nullvalue(editor) + "醫師於" + displayTime + "更新"
        }
        const { EditMode } = this.state
        const { value } = this.props
        if (!EditMode) {
            return (
                <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
                    <Icon onClick={() => this.setState({ EditMode: true })} type="edit" />
                    <Icon onClick={() => this.contextDelete()} type="delete" />
                    <Tooltip placement="topRight" title={tooltiptext(value.editor, value.patientimportantmatterkey.time)}>
                        <Icon type="info-circle" />
                    </Tooltip>
                </div>
            );
        }
        else {
            return (
                <div style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
                    <Icon type="save" onClick={() => this.onClickSave()} />
                    <Icon onClick={() => this.setState({ EditMode: false })} type="close-circle" />
                    <Tooltip placement="topRight" title={tooltiptext(value.editor, value.patientimportantmatterkey.time)}>
                        <Icon type="info-circle" />
                    </Tooltip>
                </div>
            );
        }
    }
    render() {
        const { value } = this.props
        console.log(value)
        const cellRender = this.contextDisplay()
        const control = this.controlDisplay()
        return (
            <div style={{ display: "grid", gridTemplateColumns: "15fr 1fr", padding: "2px 5px", gridGap: "5px" }}>
                {cellRender}
                {control}
            </div>
        );
    }
}