import React, { Component } from 'react';
import detaillogo from '../Image/svg/details.svg'
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Tooltip, Input, Button, Icon } from 'antd';
import './Schedule_event.scss'
import * as ApiProvider from '../API/ApiProvider';
//資料
// import { eventdata } from '../jsonResponse'
import moment from 'moment';

const parseBedNumber = (bedNumber) => {
    if (Number.isInteger(bedNumber)) {
        return bedNumber > 9 ? bedNumber : `0${bedNumber.toString()}`;
    }

    return '00'
}

class Schedule_event_list extends Component {
    state = {
        detailstate: "none",
        // pressitemstate: "all",
        items_interval: 0,
        powerLogin: 0,
        EditMode: false,
        inputContent: '',
        selSchedule: '排程',

        Scheduledata: [],
        status: false,
        PickerDate: moment().valueOf(),
        TRANSINID: null,
        TimeContent: "07:30"
    }

    querySchedule(time, ID) {
        const timeDate = moment(time).format("YYYY-MM-DD")
        // const et = moment(moment(time).format("YYYY/MM/DD"), "YYYY/MM/DD").valueOf() + (24 * 60 * 60 * 1000)
        // console.log(ID, st, et)
        ApiProvider.getOnePatientSchedule(ID, timeDate)
            .then(data => {
                console.log("getOnePatientSchedule OK " + JSON.stringify(data.data));
                this.setState({ Scheduledata: data.data })
            })
            .catch(err => {
                console.log("getOnePatientSchedule NG " + JSON.stringify(err));
            });
    }

    queryALLSchedule() {

        const date = this.props.PickerDate
        const selectdate = moment(date).format("YYYY-MM-DD")
        console.log(selectdate)
        ApiProvider.getAllPatientFromDate(selectdate)
            .then(data => {
                // console.log("getOnePatientSchedule OK " + JSON.stringify(data.data));
                this.setState({ Scheduledata: data.data })
            })
            .catch(err => {
                console.log("getAllPatientFromDate NG " + JSON.stringify(err));
            })
    }
    componentDidMount() {
        this.onInputChange = this.onInputChange.bind(this);
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
        this.scheduleGet()
    }

    scheduleGet() {
        const source = this.props.source
        if (source === 1) {
            const ID = this.props.caseinfo.TRANSINID
            const date = this.props.PickerDate
            this.setState({
                PickerDate: date,
                TRANSINID: ID
            })
            this.querySchedule(date, ID)
        }
        else {
            //get ALL
            this.queryALLSchedule()
            this.timerSch = setInterval(() => this.queryALLSchedule(), 1000 * 60 * 60);

        }
    }
    componentWillUnmount() {
        clearInterval(this.timerID);
    }
    componentDidUpdate() {
        const source = this.props.source
        if (source === 1) {
            if (this.props.caseinfo.TRANSINID !== this.state.TRANSINID || this.state.PickerDate !== this.props.PickerDate) {
                this.scheduleGet()
            }
        }
    }
    selSchedule(value) {
        this.setState({ selSchedule: value })
    }
    onInputChange(event) {
        this.setState({ inputContent: event.target.value })
    }
    onTimeChange = (event) => {
        if (event.target.value !== undefined && event.target.value !== null) {
            this.setState({ TimeContent: event.target.value })
        }
    }
    onChangeEditMode() {
        this.setState({ EditMode: !this.state.EditMode })
    }
    onClickEnter() {
        console.log(this.state.PickerDate)

        const timeNowStr = moment().format("YYYYMMDDHHmmss")
        const writter = sessionStorage.getItem("user")
        const name = this.props.caseinfo.PBASINGNINFO[0].pnamec
        const hisid = this.props.caseinfo.HISID
        const date = this.props.PickerDate
        const selectdate = moment(date).format("YYYY-MM-DD")
        const selectTime = this.state.TimeContent
        if (document.getElementById("time").value === "" || document.getElementById("time").value === null || document.getElementById("time").value === undefined) {
            return null
        }
        if (this.state.inputContent === null || this.state.inputContent === undefined || this.state.inputContent === "") {
            return null
        }
        ApiProvider.createPatientSchedule(this.props.caseinfo.PBASINGNINFO[0].hbednum, this.state.inputContent, timeNowStr, this.props.caseinfo.TRANSINID, this.state.selSchedule, writter, name, hisid, selectdate, selectTime)
            .then(data => {
                console.log(" OK " + JSON.stringify(data.data))
                this.setState(
                    {
                        EditMode: false,
                        inputContent: undefined,
                        status: true
                    })
                this.querySchedule(this.state.PickerDate, this.state.TRANSINID)
                this.props.reCalender()
            })
            .catch(err => {
                console.log(" Err " + JSON.stringify(err))
            })
    }

    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }

    timechange(timestamp) {
        const changetime = new Date(timestamp).getHours() + ":" + new Date(timestamp).getMinutes()
        return changetime
    }

    cellControl(info) {
        const time = moment(info.time, "YYYYMMDDHHmmss").format('YYYY-MM-DD HH:mm:ss')
        const text = <div>{info.writter}醫師於{time}更新</div>
        if (this.state.EditMode) {
            return (
                <div style={{ display: "grid", gridTemplateColumns: "20px 20px", gridGap: "5px" }}>
                    <div style={{ height: "100%", display: "flex", justifyContent: "center", alignItems: "center", cursor: "pointer" }} onClick={() => this.onClickDelete(info)}>
                        <Icon type="delete" />
                    </div>
                    <Tooltip placement="right" title={text}>
                        <Icon type="info-circle" />
                    </Tooltip>
                </div>
            );

        }
        return (
            <Tooltip placement="right" title={text}>
                <Icon type="info-circle" />
            </Tooltip>
        );
    }

    listevent() {
        let ScheduleList = this.state.Scheduledata
        if (ScheduleList.length === 0) {
            return <div style={{ height: "50px", display: "flex", alignItems: "center" }}>當日無排程</div>
        }
        const CellRender = ScheduleList.map((info, index, arr) => {
            // const localTime = moment.utc(time).toDate()
            return (
                <div key={index} className='CellLayout'>
                    <div className='Context'>
                        <div style={{ height: '30px', borderRadius: "4px", backgroundColor: "rgba(124,179,5,1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <div style={{ color: 'white', fontSize: "16px" }}>NI-{parseBedNumber(info.bednumber)}</div>
                            {/* itemchange(info.type) */}
                        </div>
                        <div style={{ fontSize: "16px", display: "flex", alignItems: "center" }}>
                            {info.name.toString().trim()}
                        </div>
                        <div style={{ fontSize: "16px", display: "flex", alignItems: "center" }}>
                            {info.selectTime}
                        </div>
                        <div style={{ fontSize: "16px", display: "flex", alignItems: "center" }}>
                            {info.context}
                        </div>
                    </div>
                    {this.cellControl(info)}
                </div>
            );
        })
        return CellRender
    }


    //測驗項目功能_區間按鈕選擇
    Onchangetimeinterval(changestate) { this.setState({ items_interval: changestate }) }
    EditControl() {
        const edit = this.props.editstatus
        if (edit) {
            return (
                <div onClick={() => this.onChangeEditMode()} style={{ display: this.state.powerLogin == 1 ? "flex" : "none", cursor: "pointer" }}>
                    {/* <img src={editlogo} alt="editlogo" /> */}
                    <Icon type="plus-circle" />
                </div>
            );
        }
        return null
    }
    onClickDelete(item) {
        //Delete todo item
        ApiProvider.deletePatientSchedule(item.schedulekey.transinno, item.schedulekey.time)
            .then(data => {
                console.log("success")
                this.scheduleGet()
                this.props.reCalender()
            })
            .catch(err => console.log(err))

    }
    render() {
        const { title } = this.props
        const classes = makeStyles((theme) => ({
            container: {
                display: 'flex',
                flexWrap: 'wrap',
            },
            textField: {
                marginLeft: theme.spacing(1),
                marginRight: theme.spacing(1),
                width: 200,
            },
        }));
        const EditButton = this.EditControl()
        return (
            <div className='ScheduleEventLayout'>
                <div className='ScheduleEventTitle'>
                    <div style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>{title}</div>
                    {EditButton}
                </div>
                <div className='ScheduleEventContext'>
                    <div className='ContextList'>
                        {this.listevent()}
                    </div>
                </div>
                <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                    <div style={{ padding: "5px 10px", display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div value='排程' style={{ display: "grid", width: 150, height: "32px", justifyContent: "center", alignItems: "center", border: "1px solid #d9d9d9", borderRadius: "4px", marginRight: "5px" }}>排程</div>
                        <form className={classes.container} noValidate>
                            <TextField
                                id="time"
                                type="time"
                                defaultValue={this.state.TimeContent}
                                className={classes.textField}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                inputProps={{
                                    step: 300, // 5 min
                                }}
                                onChange={this.onTimeChange}
                            />
                        </form>
                        <Input placeholder="輸入當日內容" value={this.state.inputContent} onChange={this.onInputChange}></Input>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                            <Button onClick={() => this.onClickCancel()}>取消</Button>
                            <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Schedule_event_list
