import React, { Component } from 'react';
// import editlogo from '../../Image/svg/Edit.svg'
import detaillogo from '../../Image/svg/details.svg'
import { Tooltip, Input, Button, Icon, Modal } from 'antd';
import './Notice.scss'
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'
import NoticeCell from './NoticeCell'

class Notice extends Component {
    state = {
        caseinfo: this.props.caseinfo,
        EditMode: false,
        noticeData: [],
        inputContent: undefined,
        powerLogin: 0,
        preTRID: null,
        visible: false
    }
    componentDidMount() {
        //bind function
        this.onInputChange = this.onInputChange.bind(this);
        //query patient notice
        const TRID = this.props.caseinfo.TRANSINID.toString().trim()
        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins, preTRID: TRID })
        this.queryPatientImportant(TRID);
    }
    componentDidUpdate() {
        const TRID = this.props.caseinfo.TRANSINID.toString().trim()
        const preTRID = this.state.preTRID
        if (TRID !== preTRID) {
            this.setState({ preTRID: TRID })
            this.queryPatientImportant(TRID);
        }
    }
    queryPatientImportant(TRID) {
        ApiProvider.getOnePatientImportantMatter(TRID)
            .then(data => {
                console.log("getOnePatientImportantMatter OK " + JSON.stringify(data));
                this.setState({ noticeData: data.data });
            })
            .catch(err => {
                console.log("getOnePatientImportantMatter NG " + JSON.stringify(err));
            });
    }
    onInputChange(event) {
        this.setState({ inputContent: event.target.value })
    }
    onChangeEditMode() {
        this.setState(
            {
                EditMode: !this.state.EditMode
            }
        )
    }

    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }

    onClickEnter() {
        const timeNowStr = moment().format("YYYYMMDDHHmmss")
        const writter = sessionStorage.getItem("user")
        ApiProvider.createPatientImportantMatter(this.state.inputContent, writter, this.state.caseinfo.HISID, timeNowStr, this.state.caseinfo.TRANSINID)
            .then(data => {
                // console.log("createPatientImportantMatter OK " + JSON.stringify(data));
                this.queryPatientImportant(this.state.preTRID);
            })
            .catch(err => {
                console.log("createPatientImportantMatter NG " + JSON.stringify(err));
            });
        this.setState(
            {
                EditMode: false,
                inputContent: undefined
            }
        )
    }
    renderEditButton() {
        if (this.props.disableEdit === true) {
            return null;
        } else {
            return (
                <div onClick={() => this.onChangeEditMode()} style={{ display: this.state.powerLogin == 1 ? "flex" : "none", cursor: "pointer" }}>
                    <Icon type="plus-circle" />
                </div>
            )
        }
    }


    callBack = (value, info) => {
        console.log(value, info)
        if (info === "save") {
            ApiProvider.createPatientImportantMatter(value.context, value.editor, value.hisid, value.time, value.transinid)
                .then(data => {
                    // console.log("createPatientImportantMatter OK " + JSON.stringify(data));
                    this.queryPatientImportant(this.state.preTRID);
                })
                .catch(err => {
                    console.log("createPatientImportantMatter NG " + JSON.stringify(err));
                });
        }
        if (info === "delete") {
            ApiProvider.deletePatientImportantMatter(value.time, value.transinid)
                .then(data => { this.queryPatientImportant(this.state.preTRID); })
                .catch(err => console.log(err))
        }

    }

    remarkcellRender(dataList) {
        if (Array.isArray(dataList)) {
            if (dataList.length === 0) {
                return (
                    <div style={{ height: "50px", paddingLeft: "15px", display: "flex", alignItems: "center" }}>
                        無病人註記
                    </div>
                );
            }
            const remarkcell = dataList.map((info, index) =>
                <NoticeCell key={index} value={info} callback={this.callBack}></NoticeCell>
            )
            return remarkcell
        }
        return (
            <div style={{ height: "50px", paddingLeft: "15px", display: "flex", alignItems: "center" }}>
                無病人註記
            </div>
            // <div style={{ padding: "5px 15px" }}>無病人註記</div>
        );
    }
    render() {
        const fixData = this.state.noticeData.sort((a, b) => {
            let tempTimeA = moment(a.patientimportantmatterkey.time, 'YYYY-MM-DDThh:mm:ssZ').valueOf()
            let tempTimeB = moment(b.patientimportantmatterkey.time, 'YYYY-MM-DDThh:mm:ssZ').valueOf()
            return tempTimeA < tempTimeB ? 1 : -1
        })

        const remarkcell = this.remarkcellRender(fixData)
        const { TextArea } = Input;
        return (
            <div style={{ marginBottom: '10px' }}>
                {this.props.disableEdit
                    ?
                    <div style={{ backgroundColor: "#71b0e46e", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "36px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                        <div style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>病人註記</div>
                    </div>
                    :
                    <div className='noticetitle'>
                        <div style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>病人註記</div>
                        {this.renderEditButton()}
                    </div>}
                <div className='noticecontextborder'>
                    {remarkcell}
                </div>
                <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                    <div style={{ padding: "5px 10px", backgroundColor: "rgba(238,238,238,1)" }}>
                        <TextArea id="noticeContent" placeholder="輸入當日註記內容" value={this.state.inputContent} onChange={this.onInputChange} autoSize={true}></TextArea>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                            <Button onClick={() => this.onClickCancel()}>取消</Button>
                            <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Notice
