import React, { Component } from 'react';
import './ExamNonlabtablecell.scss'
// import { Nonlablist } from '../../Data/Wardviewdata'
import moment from 'moment'
import NonlabviewtableCell from '../CrossSectionView/NonlabviewtableCell'
// import { Modal } from 'antd';
// import InsReportModal from '../ExamReportModal'
import * as RegularData from '../../Data/RegularData'

class Examnonlabviewtable extends Component {
    state = {
        visible: false,
        reFilter: RegularData.REfilter()
    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            const resultKey = mapping.sample ? mapping.key + "-" + mapping.sample : mapping.key
            allResult[resultKey] = []
        });
        allResult["全部類別"] = []
        inputs.forEach(input => {
            let content = input.ORPROCED;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "" && (mapping.sample ? input.ORSPCN1.trim() === mapping.sample : true)) {
                    const resultKey = mapping.sample ? mapping.key + "-" + mapping.sample : mapping.key
                    allResult[resultKey].push(input)
                }
            });

            allResult["全部類別"].push(input)
        });

        return allResult;
    }

    render() {
        function ORDLISTFormat(ORDLIST, PickerDateRange, RESSECT) {
            var ORDLIST_Format = []
            if (Array.isArray(ORDLIST) && Array.isArray(RESSECT)) {
                for (let index = 0; index < ORDLIST.length; index++) {
                    const element = ORDLIST[index];
                    const elemetnStatus = Number(element.ORSTATUS)
                    const elementTime = moment(element.ORDATE.toString().trim() + element.ORTIME.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    const elementSEQ = element.ORDSEQNO

                    if (elemetnStatus === 64) {
                        //正式報告
                        const reportRessect = RESSECT.filter(function (info, index, arr) {
                            return info.ORDSEQNO === elementSEQ
                        })
                        if (reportRessect.length > 0) {
                            const reportTime = moment(reportRessect[0].ORRCPDT + reportRessect[0].ORRCPTM, "YYYY-MM-DDHH:mm:ss").valueOf()
                            if (PickerDateRange[0] <= reportTime && reportTime <= PickerDateRange[1]) {
                                ORDLIST_Format.push({
                                    time: moment(reportRessect[0].ORRCPDT + reportRessect[0].ORRCPTM, "YYYY-MM-DDHH:mm:ss").valueOf(),
                                    status: 0,
                                    element: reportRessect[0]
                                })
                            }
                        }
                        else {
                            if (PickerDateRange[0] <= elementTime && elementTime <= PickerDateRange[1]) {
                                ORDLIST_Format.push({
                                    time: elementTime,
                                    status: 0,
                                    element: element
                                })
                            }
                        }
                    }
                    else if (elemetnStatus === 38) {
                        //已簽收 
                        if (PickerDateRange[0] <= elementTime && elementTime <= PickerDateRange[1]) {
                            ORDLIST_Format.push({
                                time: elementTime,
                                status: 1,
                                element: element
                            })
                        }
                    }
                    else if (elemetnStatus === 31) {
                        //待送檢
                        if (PickerDateRange[0] <= elementTime && elementTime <= PickerDateRange[1]) {
                            ORDLIST_Format.push({
                                time: elementTime,
                                status: 2,
                                element: element
                            })
                        }
                    }
                    else if (elemetnStatus === 82 || elemetnStatus === 60 || elemetnStatus === 61) {
                        //已取消 不顯示
                        continue
                    }
                    else {
                        //Other
                        if (PickerDateRange[0] <= elementTime && elementTime <= PickerDateRange[1]) {
                            ORDLIST_Format.push({
                                time: elementTime,
                                status: elemetnStatus,
                                element: element
                            })
                        }
                    }
                }
            }
            return ORDLIST_Format
        }
        function formatSelectMode(data, selectMode) {
            //StateMode 0:全部狀態 1:正式報告 2:已簽收 3:待送檢 4:其他
            if (Array.isArray(data)) {
                if (selectMode === 1) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.status) === 0
                    })
                    return filterData
                }
                if (selectMode === 2) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.status) === 1
                    })
                    return filterData
                }
                if (selectMode === 3) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.status) === 2
                    })
                    return filterData
                }
                if (selectMode === 4) {
                    const filterData = data.filter(function (info, index, arr) {
                        return Number(info.status) !== 0 && Number(info.status) !== 1 && Number(info.status) !== 2
                    })
                    return filterData
                }
                return data
            }
            return null

        }
        function formatInputMode(data, InputMode, reFilter) {
            if (Array.isArray(data)) {
                if (InputMode === null || InputMode === "" || InputMode === undefined) {
                    return data
                }
                // console.log(InputMode)

                reFilter.map(data => {
                    let nodePlace = 0
                    while (InputMode.indexOf(data, nodePlace) !== -1) {
                        // console.log(nodePlace)
                        // console.log(InputMode.indexOf(data, nodePlace))
                        const tempPlace = InputMode.indexOf(data, nodePlace)

                        InputMode = InputMode.slice(0, tempPlace) + "\\" + InputMode.slice(tempPlace)
                        // console.log(InputMode)

                        nodePlace = tempPlace + 2
                    }
                })

                const regex = new RegExp(InputMode, "i");
                console.log(regex)
                const filterInputMode = data.filter(function (info, index, arr) {
                    return info.ORPROCED.toString().toLowerCase().match(regex) !== null
                })
                return filterInputMode
            }
            return null
        }
        const { PickerDateRange, ORDLIST, StateMode, InputMode, RESSECT } = this.props
        const { reFilter } = this.state

        const showModal = this.props.showModal
        let FormatList = formatSelectMode(ORDLISTFormat(ORDLIST, PickerDateRange, RESSECT), StateMode)

        if (FormatList.length > 1) {
            FormatList = FormatList.sort((a, b) => {
                return a.time < b.time ? 1 : -1
            })
        }

        const nonlabcell = FormatList.map(function (value, index, array) {
            const come = value.element.ORSPCN1;
            const time = value.time
            const name = value.element.ORPROCED;
            const status = value.status;
            let isSameDay = false;
            if (index < FormatList.length - 1) {
                isSameDay = moment(value.time).isSame(moment(FormatList[index + 1].time), 'day');
            }
            if (index === FormatList.length - 1) {
                isSameDay = true;
            }

            return (
                <NonlabviewtableCell key={"Nonlabviewtable" + index} come={come} time={time} name={name} status={status} hisId={value.element.ORHISTNO} caseId={value.element.ORDSEQCN} month={value.element.ORMM} seq={value.element.ORDSEQNO} showModal={showModal} isSameDay={isSameDay}></NonlabviewtableCell>
            );
        })

        console.log(FormatList)
        const title = ["時間", "檢驗項目", "檢體", "狀態", "報告"]
        const titlecell = title.map(function (value, index, arr) {
            // const tempStyle = index == 1
            return (
                <t3 className='nonlabtebletitlecell' style={{ justifyContent: "center" }}>{value}</t3>
            );
        })

        return (
            <div >
                <div style={{ height: "30px", backgroundColor: "rgba(226, 237, 243,.5)", display: "grid", gridTemplateColumns: "1fr 1.5fr 1fr 1fr 1fr" }}>
                    {titlecell}
                </div>
                <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                    <div style={{ minHeight: "calc(100vh - 305px)", maxHeight: "calc(100vh - 305px)", overflowY: "auto" }}>
                        {JSON.stringify(FormatList) !== '[]' ?
                            nonlabcell
                            :
                            <div style={{ display: "grid", gridTemplateColumns: "1fr 1.5fr 1fr 1fr 1fr" }}>
                                <div style={{ height: "50px", display: "flex", alignItems: "center", justifyContent: "center" }}>
                                    無資料
                                </div>
                            </div>}
                    </div>
                </div>
            </div>
        );
    }
}

export default Examnonlabviewtable
